// Include you library here



html, body, div, span, h1, h2, h3, h4, p, a, img, strong, i, ul, li, form, label, header, nav, section, button {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
header, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
}


